body,
html {
  text-align: center;
  background-color: #212121;
  color: white;
  font-family: 'Lato';

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.App {
  padding: 50px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

h1 {
  color: #7289DA;
  font-weight: 200;
  margin-bottom: 0px;
  margin-top: 0px;
  font-size: 48px;
}

h2 {
  color: #3b3f44;
  margin-top: 0px;
  font-size: 36px;
}

b {
  -webkit-animation: breathing 7s ease-out infinite normal;
          animation: breathing 7s ease-out infinite normal;
}

@-webkit-keyframes breathing {
  0% {
    color: #3b3f44;
  }
  50% {
    color: #7289DA;
  }
  100% {
    color: #3b3f44;
  }
}

@keyframes breathing {
  0% {
    color: #3b3f44;
  }
  50% {
    color: #7289DA;
  }
  100% {
    color: #3b3f44;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
    -webkit-transform: translate(-50%, -60%);
            transform: translate(-50%, -60%)
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
    -webkit-transform: translate(-50%, -60%);
            transform: translate(-50%, -60%)
  }
  to {
    opacity: 1;
  }
}


