.Icon {
    margin-top: 50px;
    width: 200px;
    -webkit-animation: rotate 60s cubic-bezier(0, 0, 0, 0) infinite normal;
            animation: rotate 60s cubic-bezier(0, 0, 0, 0) infinite normal;
}

@-webkit-keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotate {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


